<template lang="jade">
.wechat
 .rewrap
  .title
   p.rehead
    span 
    | 
   v-touch.rechoice(:class="{'rechoiceh':ctoggle}" @tap="toggle")
    icon(name='wechat')
    | 
  .input
   p.rehead
    span 
    | 
   .input_l
    input.t_input(placeholder="",v-model="$store.state.center.chargeMoney" type="text")
    input.t_input(style="display:block;margin-top:0.25rem",type="text",v-model="user_Remark",placeholder="")
    .input_tip   :
     span {{Remark}}
   .tip_r
    div(class="mesl mesr")
     span 
     span  : 10050000 <br>  : 105000
    .mesl
     span 
     span
 v-touch.t_btn(tag="button",@tap="transform") 
</template>
<script>
    let data ={
    	ctoggle:true,
			Remark:null,
			user_Remark:null
    }
	export default{
		data(){
			return data
		},
	  mounted:function(){
		 this.getMatch();
		},
		methods:{
			toggle(){
				this.ctoggle=!this.ctoggle
			},
			getMatch(){
				const mark=Math.ceil(Math.random()*100000);
	 		  this.Remark=mark;
			},
			transform(){
				if(this.$store.state.center.chargeMoney==''){
					this.$store.commit("centerTip",4);
				}else if(this.user_Remark==this.Remark&&this.$store.state.center.chargeMoney!==''){
					this.$store.commit("transformPay",{type:1,mark:this.Remark});
					this.user_Remark='';
					this.getMatch();
				}else if(this.user_Remark==null){
            this.$store.commit("centerTip","");
				}else if(this.user_Remark!==this.Remark&&this.user_Remark!==null){
					 this.$store.commit("centerTip","");
				}

      }
		}
	}
</script>



